body {
  overflow-y: scroll;
  background-color: #d1eeff;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.card {
  border-color: #3d0082;
}

.card-body {
  height: 220px;
  background-color: #e8ebfe;
}

.card-header {
  border-color: #3d0082;
  background-color: #495efa;
  color: #fff;
}

.bodyTotal {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sumCompanies {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  border: 2px solid #000;
  border-radius: 50%;
  background-color: #8abcfa;
  color: #fff;
  font-size: 32px;
}

.listCompanies {
  overflow-y: auto;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-left: 0;
  border-radius: 4px;
}

.list-group-item {
  padding-top: 8px;
  padding-bottom: 8px;
}

.list-group-item:first-child {
  border-top: 0;
  border-top-right-radius: 0;
}

.list-group-item:last-child {
  border-bottom: 0;
  border-bottom-right-radius: 0;
}

.partners {
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  height: 270px;
}

.partnersHeader {
  display: flex;
  justify-content: space-between;
}

.partnersBlock {
  display: none;
}

.partnersList {
  position: absolute;
  top: 20%;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 0;
  width: 220px;
  list-style-type: none;
  transform: rotate(-90deg);
}

.partnersItem {
  position: relative;
  margin: 10px;
  margin-left: 0;
  padding: 10px 15px 15px;
  width: 75%;
  border: 2px solid #000;
  border-right: 0;
  background-color: #6c5ef2;
  color: #fff;
  white-space: nowrap;
  font-size: 22px;
}

.partnersItem:after {
  position: absolute;
  top: 10%;
  left: 86%;
  z-index: -1;
  width: 46px;
  height: 46px;
  border: 2px solid #000;
  border-radius: 4px;
  background-color: #6c5ef2;
  content: "";
  transform: rotate(45deg);
}

.partnersPercent {
  position: absolute;
  top: 3%;
  left: 110%;
  z-index: -2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 55px;
  height: 55px;
  border: 2px solid #000;
  border-radius: 50%;
  background-color: #412fee;
  color: #fff;
  font-size: 20px;
  writing-mode: tb-rl;
}

.sortContent {
  padding: 0 10px;
}

.sortBtn {
  position: relative;
  overflow: hidden;
  width: 13px;
  height: 10px;
  color: #b5b3b2;
  cursor: pointer;
}

.sortBtnBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  font-size: 22px;
}

.sortBtnUp {
  margin-bottom: 5px;
}

.sortBtnDown:before {
  position: absolute;
  top: -11px;
}

.sortBtnActive {
  color: #000;
}

.exit {
  margin-left: 20px;
}

.exitBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
}

.bodyLocation {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.pieLabel {
  font-weight: bold;
  cursor: pointer;
}

.pieLabel div:hover {
  text-decoration: underline;
}

.graphContainer {
  position: relative;
  height: 210px;
}

.graphPlaceholder {
  width: 100%;
  height: 100%;
}

.graphList {
  overflow-y: auto;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-left: 0;
  border-radius: 4px;
}

.graphListContainer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  margin: 20px;
}

.graphBack {
  position: absolute;
  right: 20px;
  display: none;
  font-size: 25px;
  cursor: pointer;
}

.graphBack:hover {
  color: #bbd8fc;
}

#graph {
  width: 210px;
}

#graph span:nth-of-type(3) {
  left: -5px !important;
}

.newsBody {
  position: relative;
  overflow: hidden;
}

.newsContent {
  display: flex;
  height: 100%;
  font-size: 15px;
}

.newsMedia {
  display: flex;
  flex: 2;
  flex-direction: column;
  font-size: 13px;
}

.newsAuthor {
  margin-top: 3px;
}

.newsAuthor,
.newsPublic {
  padding: 2px;
}

.newsAuthor span,
.newsPublic span {
  padding-right: 5px;
  font-weight: bold;
  font-style: italic;
}

.newsImage {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  width: 100%;
  height: 100px;
}

.newsImage div {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #b5b3b2 no-repeat center;
  background-size: cover;
}

.newsText {
  flex: 3;
  padding-left: 20px;
}

.newsTitle {
  font-weight: bold;
  font-size: 18px;
}

.newsDescription {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  -webkit-line-clamp: 6;
}

.carousel {
  position: absolute;
  top: 20px;
  right: 20px;
  bottom: 0;
  left: 20px;
}

.carousel-inner {
  height: 82%;
}

.carousel-indicators {
  bottom: 0;
}

.carousel-item {
  height: 100%;
}

.carousel-indicators li {
  background-color: #b5b3b2;
}

.carousel-indicators .active {
  background-color: #000;
}

@media only screen and (max-width: 576px) {
  .partnersHeader {
    flex-direction: column;
  }

  .partnersHeader span {
    display: block;
    padding-bottom: 10px;
    text-align: center;
  }

  .sortContent {
    padding: 0 5px;
  }

  .sortPanel {
    justify-content: center;
  }

  .exit {
    margin-left: 5px;
  }
}